<template>
  <div class="user" id="join">
    <div class="wrapC table">
      <img class="signup-image" src="img\signup.png" alt />
      <div class="middle">
        <div class="form-wrap">
          <div class="input-id">
            <input v-model="nickName" id="nickname" placeholder=" " type="text" />
            <button class="check-nickname" @click="checkNickname" v-if="!nicknameoverlapcheck"></button>
            <button class="reset-nickname" @click="resetNickname" v-else> </button>
            <p>*    </p>
          </div>

          <div class="input-wrap">
            <input v-model="email" id="email" placeholder=" " type="text" />
            <button class="check-email" @click="checkEmail" v-if="!emailoverlapcheck"></button>
            <button class="reset-email" @click="resetEmail" v-else> </button>
          </div>

          <div class="input-wrap password-wrap">
            <input v-model="password" id="password" :type="passwordType" placeholder=" " />
            <span :class="{ active: passwordType === 'text' }">
              <i class="fas fa-eye" @click="viewpassword"></i>
            </span>
          </div>

          <div class="input-wrap password-wrap">
            <input
              v-model="passwordConfirm"
              id="password-confirm"
              :type="passwordConfirmType"
              placeholder="   "
            />
            <span :class="{ active: passwordConfirmType === 'text' }">
              <i class="fas fa-eye" @click="viewpasswordConfirm"></i>
            </span>
          </div>
        </div>
        <label>
          <input
            v-model="isTerm"
            data-toggle="modal"
            data-target="#staticBackdrop"
            type="checkbox"
            id="term"
          />
          <span class="term-span"> </span>
        </label>

        <span class="go-term" data-toggle="modal" data-target="#staticBackdrop"> </span>
        <div
          class="modal fade"
          id="staticBackdrop"
          data-backdrop="static"
          data-keyboard="false"
          tabindex="-1"
          role="dialog"
          aria-labelledby="staticBackdropLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog modal-dialog-scrollable">
            <div class="modal-content">
              <div class="modal-header">
                <h3 class="modal-title" id="staticBackdropLabel"></h3>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <TermsOfUse />
              </div>
              <div class="modal-footer">
                <button
                  type="button"
                  class="modal-btn-secondary rounded-lg"
                  data-dismiss="modal"
                  v-on:click="isTerm = true"
                ></button>
                <button
                  type="button"
                  class="modal-btn-primary rounded-lg"
                  data-dismiss="modal"
                  v-on:click="isTerm = false"
                ></button>
              </div>
            </div>
          </div>
        </div>
        <button class="btn" @click="signup">
          <span></span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/user.scss";
import constants from "../../lib/constants";
import axios from "axios";
import TermsOfUse from "@/page/user/TermsOfUse";
export default {
  nama: "Join",
  components: { TermsOfUse },
  created() {},
  methods: {
    signup() {
      const signupData = {
        useremail: this.email,
        username: this.nickName,
        password: this.password,
      };
      if (this.checkSingupData(signupData)) {
        if (this.nicknameoverlapcheck) {
          if (this.emailoverlapcheck) {
            axios
              .post(constants.baseUrl + "user/signup", signupData)
              .then(() => {
                this.alertAfterpush();
              })
              .catch((err) => {
                console.log(err);
              });
          } else {
            alert("  ");
          }
        } else {
          alert("  ");
        }
      }
    },
    alertAfterpush() {
      alert(
        " .   Login  ."
      );
      this.$router.push({ name: constants.URL_TYPE.USER.LOGIN });
      window.close();
    },
    checkEmail(event) {
      if (this.reg.test(this.email)) {
        if (!this.email.trim().length) {
          alert("  .");
        } else {
          axios
            .get(constants.baseUrl + "user/check/email", {
              params: {
                email: this.email,
              },
            })
            .then((res) => {
              if (res.data === false) {
                alert(" .");
              } else {
                this.seteamil = this.email;
                this.emailoverlapcheck = true;
                const inputEmail = document.querySelector("#email");
                inputEmail.setAttribute("disabled", true);
                alert("   .");
              }
            });
        }
      } else {
        alert("  .");
      }
    },
    resetEmail(event) {
      this.seteamil = "";
      this.emailoverlapcheck = false;
      const inputEmail = document.querySelector("#email");
      inputEmail.removeAttribute("disabled");
      inputEmail.setAttribute("style", "background-color:white");
    },
    checkNickname(event) {
      if (!this.nickName.trim().length) {
        alert("  .");
      } else {
        axios
          .get(constants.baseUrl + "user/check/nickname", {
            params: {
              nickname: this.nickName,
            },
          })
          .then((res) => {
            if (res.data === false) {
              alert(" .");
            } else {
              this.setnickname = this.nickName;
              this.nicknameoverlapcheck = true;
              const inputNickname = document.querySelector("#nickname");
              inputNickname.setAttribute("disabled", true);
              alert("  .");
            }
          });
      }
    },
    resetNickname() {
      console.log("");
      this.setnickname = "";
      this.nicknameoverlapcheck = false;
      const inputNickname = document.querySelector("#nickname");
      inputNickname.removeAttribute("disabled");
      inputNickname.setAttribute("style", "background-color:white");
    },

    ///  
    viewpassword(event) {
      if (this.passwordType == "text") {
        this.passwordType = "password";
        event.target.classList.add("fa-eye");
        event.target.classList.remove("fa-eye-slash");
      } else {
        this.passwordType = "text";
        event.target.classList.add("fa-eye-slash");
        event.target.classList.remove("fa-eye");
      }
    },
    viewpasswordConfirm(event) {
      if (this.passwordConfirmType == "text") {
        this.passwordConfirmType = "password";
        event.target.classList.add("fa-eye");
        event.target.classList.remove("fa-eye-slash");
      } else {
        this.passwordConfirmType = "text";
        event.target.classList.add("fa-eye-slash");
        event.target.classList.remove("fa-eye");
      }
    },
    checkSingupData(signupData) {
      if (this.isTerm) {
        if (signupData.password === this.passwordConfirm) {
          if (signupData.password.length >= 8) {
            if (
              signupData.email !== "" &&
              this.reg.test(signupData.useremail)
            ) {
              if (
                signupData.username.length >= 3 &&
                signupData.username.length <= 15
              ) {
                return true;
              } else {
                alert(" 3  15  ");
              }
            } else {
              alert("  .");
            }
          } else {
            alert("  .");
          }
        } else {
          alert("    .");
        }
      } else {
        alert("  ");
      }
    },
  },
  watch: {},
  data: () => {
    return {
      constants,
      email: "",
      nickName: "",
      setnickname: "",
      seteamil: "",
      nicknameoverlapcheck: false,
      emailoverlapcheck: false,
      password: "",
      passwordConfirm: "",
      isTerm: false,
      passwordType: "password",
      passwordConfirmType: "password",
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
    };
  },
};
</script>
